export default {
	data() {
		return {
			addressData: {
				name: '',
				telephone: '',
				addressName: '在地图选择',
				address: '',
				detailAddress: '',
				defaule: false
			}
		}
	},
	onLoad(option){
		let title = '新增收货地址';
		if(option.type==='edit'){
			title = '编辑收货地址'
			
			this.addressData = JSON.parse(option.data)
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {
		switchChange(e){
			this.addressData.defaule = e.detail.value;
		},
		
		//地图选择地址
		chooseLocation(){
			uni.chooseLocation({
				success: (data)=> {
					this.addressData.addressName = data.name;
					this.addressData.address = data.name;
				}
			})
		},
		
		//提交
		confirm(){
			let data = this.addressData;
			if(!data.username){
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.telephone)){
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			if(!data.address){
				this.$api.msg('请在地图选择所在位置');
				return;
			}
			if(!data.detailAddress){
				this.$api.msg('请填写门牌号信息');
				return;
			}
			
			let WX_TOKEN = '';
			let userId = '';
			try {
			    const userInfo = uni.getStorageSync('userInfo');
			    if (userInfo.id) {
					userId = userInfo.id;
			    }
				const token = uni.getStorageSync('WX_TOKEN');
				if (token) {
					WX_TOKEN = token;
				}
			} catch (e) {}
			
			let that = this;
			uni.request({
			   url: that.baseUrl + '/usr/address/' + that.manageType,
			   data: {
				   id: data.id,
				   userId: userId,
				   username: data.username,
				   telephone: data.telephone,
				   address: data.address,
				   detailAddress: data.detailAddress,
				   defaule: data.defaule ? '0' : '1',
			   },
			   method: that.manageType == 'add' ? 'POST' : 'PUT',
			   header: {
				   'content-type': 'application/json',
				   'X-Access-Token': WX_TOKEN,
			   },
			   success: (res) => {
				   if(res.data.success){
					   that.$api.prePage().refreshList();
					   that.$api.msg(`地址${that.manageType=='edit' ? '修改': '添加'}成功`);
					   setTimeout(()=>{
					   	uni.navigateBack()
					   }, 500)
				   }else{
					   that.$api.msg(res.data.message);
				   }
				},
			});
		},
	}
}
